.robot-table{position:relative;overflow:auto;border-bottom:1px solid #ddd}
.robot-table table td{vertical-align:middle;padding-top:5px;padding-bottom:5px;line-height:24px}
.robot-table table td i{margin-top:0}
.robot-table .vue-switch{vertical-align:middle;margin-left:10px}
.robot-table td,.robot-table th{white-space:nowrap}
.robot-title .fr{margin:-10px 10px 0 0}
.robot-title i.icon,.robot-top-box i.icon{width:15px;height:15px;margin-right:5px;display:inline-block;vertical-align:middle}
.robot-title .btn{padding:10px 15px;border:1px solid #50d2c2;font-weight:400;font-size:14px}
.robot-title .btn.btn-default{border:1px solid #50d2c2;background:#fff;color:#50d2c2}
.robot-title i.icon{background-size:contain}
.robot-top-box{padding:20px;border-bottom:2px solid #50d2c2;background:#fff}
.robot-top-box .btn{padding:10px 15px;border:1px solid #50d2c2;font-size:14px}
.robot-top-box .btn.btn-default{border:1px solid #50d2c2;background:#fff;color:#50d2c2}
.robot-top-box i.icon{background-size:contain}
.robot-top-box i.filterGray-icon{width:20px;height:20px;background:url(../images/filter_gray.png) no-repeat;background-size:contain}
.robot-form,.robot-wrap{background:#fff}
.robot-wrap .table-tlt{border-bottom:1px solid #e7e7e7}
.filter-btn-gray{display:inline-block;padding:6px 15px;line-height:21px;vertical-align:middle;font-weight:400;background:#fff;color:#666;border:1px solid #ddd;border-radius:5px;font-size:14px}
.robot-filter-box{height:280px;margin-top:-140px}
.robot-form{padding:10px 0}
.form-row{padding:10px;line-height:40px;font-size:14px}
.form-name{float:left;text-align:right;width:140px}
.form-name.required:after{content:'*';color:red}
.form-info{margin-left:155px}
.form-info input[type=text],.form-info select{padding:10px;width:350px;-moz-box-sizing:border-box;box-sizing:border-box;line-height:20px;border:1px solid #ddd;border-radius:5px}
.form-info .input-time{width:auto}
.form-info .input-time .icon-time{top:12px}
.form-info .input-time:hover .icon-times{display:block}
.form-info .input-time .icon-times{display:none;position:absolute;top:12px;right:33px;font-size:20px;background:#d4d4d4;line-height:16px;width:18px;border-radius:10px;text-align:center;height:18px;color:#fff;cursor:pointer}
.error .error-text,.form-info .input-select,.robot-add-time span,.sources-text{display:inline-block}
.form-info .interval{margin:0 5px}
.form-info .input-select select{width:159px}
.form-info.error input[type=text],.form-info.error select{border-color:red}
.error-text{margin-left:10px;font-size:12px;color:red}
.time-select-box{margin-bottom:10px}
.time-select-box:last-child{margin-bottom:0}
.time-select-box .close{font-size:24px;margin-left:10px}
.time-select-box .tip{font-size:12px;color:#999;margin-left:20px}
.robot-add-time{color:#50d2c2}
.robot-add-time span{width:30px;height:30px;margin-right:10px;font-size:30px;line-height:26px;font-weight:100;text-align:center;vertical-align:middle;color:#4fd3c2;border:1px solid #ddd}
.sources-text{position:relative;width:350px;padding:5px 30px 5px 10px;line-height:30px;min-height:40px;-moz-box-sizing:border-box;box-sizing:border-box;border:1px solid #ddd;border-radius:5px}
.sources-text:after{content:'+';position:absolute;right:10px;top:48%;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%);font-size:30px;font-weight:100;color:#666}
.robot-tag-title{padding-top:12px;-moz-box-sizing:border-box;box-sizing:border-box}
.tag-box-bg{width:360px;margin:0 auto;height:34px;line-height:34px;border-radius:5px}
.tag-box-bg .tag-list{float:left;width:120px;-moz-box-sizing:border-box;box-sizing:border-box;text-align:center;font-weight:400;color:#50d2c2;border:1px solid #50d2c2;border-left:0;cursor:pointer}
.tag-box-bg .tag-list.active{background:#50d2c2;color:#fff}
.tag-box-bg .tag-list:first-child{border-top-left-radius:5px;border-bottom-left-radius:5px;border-left:1px solid #50d2c2}
.tag-box-bg .tag-list:last-child{border-top-right-radius:5px;border-bottom-right-radius:5px}
.robotSoucesWrap{height:500px;margin-top:-250px}
.taskSouces{height:316px;overflow-y:auto;line-height:24px;padding:10px 20px}
.taskSouces .checkbox-label,.taskSouces .radio-wrap{position:relative;display:block;margin:10px 0}
.taskSouces .checkbox-label{padding-left:24px}
.taskSouces .checkbox-label input[type=checkbox]+i{position:absolute;left:0;top:50%;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%)}
.radio-info{text-align:left}
.radio-info i{display:inline-block;width:17px;height:16px;vertical-align:middle;background:url(../images/group.png)}
.radio-info.import-state i{background-image:url(../images/group-u.png)}
.btn-center-block{margin:10px 0;padding:0 50px;text-align:center}
.btn-center-block .btn{margin:0 10px}
.btn-center-block .btn-wrap{margin:0 90px}
.btn-center-block .link{font-size:14px;line-height:34px}
.taskSouces-Statistics{padding:0 20px;background:#f6f6f6;line-height:40px;font-size:14px}
.rate-box,.robot-search{line-height:34px;font-size:14px;display:inline-block}
.taskSouces-Statistics .checkbox-box{position:relative;padding-left:30px}
.taskSouces-Statistics .gray{color:#999}
a.link.disabled,table tr.disabled{color:#bbb}
.robot-modify-right{margin-top:-9px;margin-right:15px;font-weight:400}
.robot-modify-right a{margin-right:15px;font-size:14px}
.robot-search .search-name{display:inline-block;font-weight:bolder}
.robot-search .checkbox-box{position:relative;padding-left:25px}
.robot-detail-right{margin-top:-8px;margin-right:15px;font-weight:400}
.rate-box{margin-left:15px}
.rate-box .icon-rate{display:inline-block;width:34px;height:34px;margin-right:10px;vertical-align:middle}
.rate-box .icon-rate.connectionRate{background-image:url(../images/connectionRate.png)}
.rate-box .icon-rate.intentionRate{background-image:url(../images/intentionalRate.png)}
.rate-box .icon-rate.finishedRate{background-image:url(../images/finishedRate.png)}
.rate-box label{color:#50d2c2}
.robot-search-box{display:inline-block;margin-left:10px}
.robot-search-box a{float:right;width:56px;text-align:center;border:1px solid #ddd;border-left:0;color:#50d2c2;font-weight:bolder}
.robot-search-box input[type=text]{padding:0 10px;height:34px;border:1px solid #ddd}
.th-tip{display:inline-block;position:relative;margin-right:-20px}
.th-tip .icon-tip{display:inline-block;width:20px;height:20px;vertical-align:middle;cursor:pointer;background:url(../images/i-tip.png)}
.th-tip:hover .th-tip-box{display:block}
.th-tip-box{display:none;position:absolute;right:-31px;top:39px;width:130px;padding:10px;background:#ededed;border-radius:5px;color:#333;font-size:12px;line-height:22px}
.th-tip-box:before{content:'';position:absolute;top:-5px;right:35px;border-width:0 5px 5px;border-style:solid;border-color:transparent transparent #ededed}
.th-tip-box .tip-title{font-size:14px;line-height:30px}
.redistribute{position:relative;display:inline-block;padding:0 10px;margin-left:20px;border:1px solid #ddd;border-radius:5px;background:#f7f9fa;font-weight:400;color:#bbb}
.redistribute .icon-redistribute{display:inline-block;width:19px;height:18px;margin-right:10px;vertical-align:sub;background:url(../images/reDistribute.png)}
.robot-btn,.robot-btn i.icon,.robot-tag{vertical-align:middle;display:inline-block}
.redistribute.active{border-color:#50d2c2;background:#50d2c2;color:#fff;cursor:pointer}
.redistribute.active .icon-redistribute{background:url(../images/reDistribute-active.png)}
.redistribute .redistribute-wrap{position:absolute;z-index:9;left:0;top:40px;color:#666;padding:10px;background:#fff;box-shadow:0 2px 10px 0 rgba(10,2,4,.1)}
.robot-btn,.robot-list table td,.robot-list table th{padding:0 10px}
.robot-tag-box{margin-top:5px}
.robot-tag{position:relative;margin:0 20px;line-height:27px;font-size:14px;border-bottom:2px solid transparent;color:#999}
.robot-tag.robot-tag-active{color:#56d1c2;border-bottom-color:#56d1c2}
.robot-tag:after{content:'|';position:absolute;right:-22px;top:0;color:#999}
.robot-tag:last-child::after{content:''}
.form-info .choose-add{line-height:16px}
.form-info .choose-add>li{margin-top:0;margin-bottom:12px}
.form-info input[type=text]:disabled,.form-info select:disabled{background:#f0f0f0}
.form-info .sources-text[disabled=disabled]{background:#f0f0f0;color:#999}
.robot-btn{margin-left:10px;background:#e0fcf9;border-radius:4px;border:1px solid #e0fcf9;color:#56d1c2}
.robot-btn i.icon{width:18px;height:16px;background-repeat:no-repeat}
.robot-btn i.icon.icon-export{background-image:url(../images/export.png)}
.robot-btn i.icon.icon-delete{width:14px;height:18px;background-image:url(../images/delete.png)}
.robot-list{margin:20px 30px;font-size:14px;line-height:40px;max-height:300px;min-height:100px;overflow:auto}
.robot-list table{width:100%;border:1px solid #f0f0f0}
.robot-list table th{font-weight:400;text-align:left;border-right:1px solid #f0f0f0}
.robot-list table tr{border-bottom:1px solid #f0f0f0}
.robot-list table tr:hover{background:#f7fdfc}
.robot-list table td:first-child,.robot-list table th:first-child{width:50px;text-align:center}
.robot-list table td:first-child .checkbox-label,.robot-list table th:first-child .checkbox-label{margin:0}
.taskSouces-type{margin:10px 0 20px;text-align:center}
.taskSouces-type .checkbox-label{display:inline-block}
.form-row .choose-add{padding:0}
.form-box{margin-top:15px}
.form-box:first-child{margin-top:0}

